            </ul>
          </div>

          <div class="clearfix">
            <div class="nav-label pull-left">院校层级：</div>
            <ul class="label-con">
              <li class="label-object" v-for="(item, index) in schoolLevel" :class="{active: item.current}"
                  v-on:click="searchLevel(item)">{{item.levelName}}
              </li>
